<template>
  <el-container style="width: 100vw; height: 100vh">
    <el-header
      style="
        background: #f5f5f5;
        display: flex;
        height: 40px;
        border: 1px solid #e8e8e8;
        align-items: center;
      "
    >
      <div style="display: flex; align-items: center" v-if="ofdObj">
        <div class="scale-icon" style="margin-left: 10px" @click="plus">
          <font-awesome-icon icon="search-plus" />
        </div>
        <div class="scale-icon" @click="minus">
          <font-awesome-icon icon="search-minus" />
        </div>
        <div class="scale-icon">
          <font-awesome-icon icon="step-backward" @click="firstPage" />
        </div>
        <div class="scale-icon" style="font-size: 18px" @click="prePage">
          <font-awesome-icon icon="caret-left" />
        </div>
        <div class="scale-icon">{{ pageIndex }}/{{ pageCount }}</div>
        <div class="scale-icon" style="font-size: 18px" @click="nextPage">
          <font-awesome-icon icon="caret-right" />
        </div>
        <div class="scale-icon" @click="lastPage">
          <font-awesome-icon icon="step-forward" />
        </div>
      </div>
    </el-header>
    <el-main
      style="height: auto; background: #808080; padding: 0"
      v-loading="loading"
    >
      <div
        class="main-section"
        id="content"
        ref="contentDiv"
        @mousewheel="scrool"
      ></div>
    </el-main>
    <div
      class="SealContainer"
      id="sealInfoDiv"
      hidden="hidden"
      ref="sealInfoDiv"
    >
      <div class="SealContainer mask" @click="closeSealInfoDialog"></div>
      <div class="SealContainer-layout">
        <div class="SealContainer-content">
          <p class="content-title"></p>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSigner">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spProvider">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span
              class="value"
              id="spHashedValue"
              @click="showMore('', 'spHashedValue')"
              style="cursor: pointer"
              >[]</span
            >
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span
              class="value"
              id="spSignedValue"
              @click="showMore('', 'spSignedValue')"
              style="cursor: pointer"
              >[]</span
            >
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSignMethod">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spVersion">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="VerifyRet">[]</span>
          </div>

          <p class="content-title"></p>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealID">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealName">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealType">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealAuthTime">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealMakeTime">[]</span>
          </div>
          <div class="subcontent">
            <span class="title"></span>
            <span class="value" id="spSealVersion">[]</span>
          </div>
        </div>
        <input
          style="position: absolute; right: 1%; top: 1%"
          type="button"
          name=""
          id=""
          value="X"
          @click="closeSealInfoDialog()"
        />
      </div>
    </div>

    <el-dialog :title="title" :visible.sync="dialogFormVisible">
      <span style="text-align: left">
        {{ value }}
      </span>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible = false"
          > </el-button
        >
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import {
  parseOfdDocument,
  renderOfd,
  renderOfdByScale,
  digestCheck,
  getPageScale,
  setPageScale,
} from "@/utils/ofd/ofd";
import * as JSZipUtils from "jszip-utils";

export default {
  name: "HelloWorld",
  data() {
    return {
      ofdBase64: null,
      loading: false,
      pageIndex: 1,
      pageCount: 0,
      scale: 0,
      title: null,
      value: null,
      dialogFormVisible: false,
      ofdObj: null,
      screenWidth: document.body.clientWidth,
    };
  },

  created() {
    this.file = null;
  },

  mounted() {
    this.screenWidth = document.body.clientWidth;
    let that = this;
    this.$refs.contentDiv.addEventListener("scroll", this.scrool);
    window.onresize = () => {
      return (() => {
        that.screenWidth = document.body.clientWidth - 88;
        const divs = renderOfd(that.screenWidth, that.ofdObj);
        that.displayOfdDiv(divs);
      })();
    };
    this.loadOfd();
  },

  methods: {
    scrool() {
      let scrolled =
        this.$refs.contentDiv.firstElementChild?.getBoundingClientRect()?.top -
        60;
      let top = 0;
      let index = 0;
      for (let i = 0; i < this.$refs.contentDiv.childElementCount; i++) {
        top +=
          Math.abs(
            this.$refs.contentDiv.children
              .item(i)
              ?.style.height.replace("px", "")
          ) +
          Math.abs(
            this.$refs.contentDiv.children
              .item(i)
              ?.style.marginBottom.replace("px", "")
          );
        if (Math.abs(scrolled) < top) {
          index = i;
          break;
        }
      }
      this.pageIndex = index + 1;
    },

    closeSealInfoDialog() {
      this.$refs.sealInfoDiv.setAttribute("style", "display: none");
      document.getElementById("spSigner").innerText = "[]";
      document.getElementById("spProvider").innerText = "[]";
      document.getElementById("spHashedValue").innerText = "[]";
      document.getElementById("spSignedValue").innerText = "[]";
      document.getElementById("spSignMethod").innerText = "[]";
      document.getElementById("spSealID").innerText = "[]";
      document.getElementById("spSealName").innerText = "[]";
      document.getElementById("spSealType").innerText = "[]";
      document.getElementById("spSealAuthTime").innerText = "[]";
      document.getElementById("spSealMakeTime").innerText = "[]";
      document.getElementById("spSealVersion").innerText = "[]";
      document.getElementById("spVersion").innerText = "[]";
      document.getElementById("VerifyRet").innerText = "[]";
    },

    showMore(title, id) {
      this.dialogFormVisible = true;
      this.value = document.getElementById(id).innerText;
      this.title = title;
    },

    plus() {
      setPageScale(++this.scale);
      const divs = renderOfdByScale(this.ofdObj);
      this.displayOfdDiv(divs);
    },

    minus() {
      setPageScale(--this.scale);
      const divs = renderOfdByScale(this.ofdObj);
      this.displayOfdDiv(divs);
    },

    prePage() {
      let contentDiv = document.getElementById("content");
      let ele = contentDiv.children.item(this.pageIndex - 2);
      ele?.scrollIntoView(true);
      ele ? (this.pageIndex = this.pageIndex - 1) : "";
    },

    firstPage() {
      let contentDiv = document.getElementById("content");
      let ele = contentDiv.firstElementChild;
      ele?.scrollIntoView(true);
      ele ? (this.pageIndex = 1) : "";
    },

    nextPage() {
      let contentDiv = document.getElementById("content");
      let ele = contentDiv.children.item(this.pageIndex);
      ele?.scrollIntoView(true);
      ele ? ++this.pageIndex : "";
    },

    lastPage() {
      let contentDiv = document.getElementById("content");
      let ele = contentDiv.lastElementChild;
      ele?.scrollIntoView(true);
      ele ? (this.pageIndex = contentDiv.childElementCount) : "";
    },

    parseQueryString(query) {
      const parts = query.split("&");
      const params = Object.create(null);

      for (let i = 0, ii = parts.length; i < ii; ++i) {
        const param = parts[i].split("=");
        const key = param[0].toLowerCase();
        const value = param.length > 1 ? param[1] : null;
        params[decodeURIComponent(key)] = decodeURIComponent(value);
      }

      return params;
    },

    loadOfd() {
      const queryString = document.location.search.substring(1);
      const params = (0, this.parseQueryString)(queryString);
      const ofdFile = params.file;
      const filename = params.filename;
      document.title = filename;
      let that = this;
      JSZipUtils.getBinaryContent(ofdFile, function (err, data) {
        if (err) {
          console.log(err);
        } else {
          let base64String = btoa(
            String.fromCharCode.apply(null, new Uint8Array(data))
          );
          that.ofdBase64 = base64String;
        }
      });
      this.getOfdDocumentObj(ofdFile, this.screenWidth);
    },

    getOfdDocumentObj(file, screenWidth) {
      let that = this;
      let t = new Date().getTime();
      this.loading = true;
      parseOfdDocument({
        ofd: file,
        success(res) {
          console.log(res);
          let t1 = new Date().getTime();
          console.log("ofd", t1 - t);
          that.ofdObj = res[0];
          that.pageCount = res[0].pages.length;
          const divs = renderOfd(screenWidth, res[0]);
          let t2 = new Date().getTime();
          console.log("xmlsvg", t2 - t1);
          that.displayOfdDiv(divs);
          let t3 = new Date().getTime();
          console.log("svg", t3 - t2);
          that.loading = false;
        },
        fail(error) {
          that.loading = false;
          that.$alert("OFD", error, {
            confirmButtonText: "",
            callback: (action) => {
              this.$message({
                type: "info",
                message: `action: ${action}`,
              });
            },
          });
        },
      });
    },

    displayOfdDiv(divs) {
      this.scale = getPageScale();
      let contentDiv = document.getElementById("content");
      contentDiv.innerHTML = "";
      for (const div of divs) {
        contentDiv.appendChild(div);
      }
      for (let ele of document.getElementsByName("seal_img_div")) {
        this.addEventOnSealDiv(
          ele,
          JSON.parse(ele.dataset.sesSignature),
          JSON.parse(ele.dataset.signedInfo)
        );
      }
    },

    addEventOnSealDiv(div, SES_Signature, signedInfo) {
      try {
        global.HashRet = null;
        global.VerifyRet = signedInfo.VerifyRet;
        div.addEventListener("click", function () {
          document.getElementById("sealInfoDiv").hidden = false;
          document
            .getElementById("sealInfoDiv")
            .setAttribute(
              "style",
              "display:flex;align-items: center;justify-content: center;"
            );
          if (SES_Signature.realVersion < 4) {
            document.getElementById("spSigner").innerText =
              SES_Signature.toSign.cert["commonName"];
            document.getElementById("spProvider").innerText =
              signedInfo.Provider["@_ProviderName"];
            document.getElementById(
              "spHashedValue"
            ).innerText = SES_Signature.toSign.dataHash.replace(/\n/g, "");
            document.getElementById(
              "spSignedValue"
            ).innerText = SES_Signature.signature.replace(/\n/g, "");
            document.getElementById(
              "spSignMethod"
            ).innerText = SES_Signature.toSign.signatureAlgorithm.replace(
              /\n/g,
              ""
            );
            document.getElementById("spSealID").innerText =
              SES_Signature.toSign.eseal.esealInfo.esID;
            document.getElementById("spSealName").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.name;
            document.getElementById("spSealType").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.type;
            document.getElementById("spSealAuthTime").innerText =
              " " +
              SES_Signature.toSign.eseal.esealInfo.property.validStart +
              "  " +
              SES_Signature.toSign.eseal.esealInfo.property.validEnd;
            document.getElementById("spSealMakeTime").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.createDate;
            document.getElementById("spSealVersion").innerText =
              SES_Signature.toSign.eseal.esealInfo.header.version;
          } else {
            document.getElementById("spSigner").innerText =
              SES_Signature.cert["commonName"];
            document.getElementById("spProvider").innerText =
              signedInfo.Provider["@_ProviderName"];
            document.getElementById(
              "spHashedValue"
            ).innerText = SES_Signature.toSign.dataHash.replace(/\n/g, "");
            document.getElementById(
              "spSignedValue"
            ).innerText = SES_Signature.signature.replace(/\n/g, "");
            document.getElementById(
              "spSignMethod"
            ).innerText = SES_Signature.signatureAlgID.replace(/\n/g, "");
            document.getElementById("spSealID").innerText =
              SES_Signature.toSign.eseal.esealInfo.esID;
            document.getElementById("spSealName").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.name;
            document.getElementById("spSealType").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.type;
            document.getElementById("spSealAuthTime").innerText =
              " " +
              SES_Signature.toSign.eseal.esealInfo.property.validStart +
              "  " +
              SES_Signature.toSign.eseal.esealInfo.property.validEnd;
            document.getElementById("spSealMakeTime").innerText =
              SES_Signature.toSign.eseal.esealInfo.property.createDate;
            document.getElementById("spSealVersion").innerText =
              SES_Signature.toSign.eseal.esealInfo.header.version;
          }
          document.getElementById("spVersion").innerText =
            SES_Signature.toSign.version;
          document.getElementById("VerifyRet").innerText =
            "... " +
            (global.VerifyRet ? "" : "");
          if (
            global.HashRet == null ||
            global.HashRet == undefined ||
            Object.keys(global.HashRet).length <= 0
          ) {
            setTimeout(function () {
              const signRetStr = global.VerifyRet
                ? ""
                : "";
              global.HashRet = digestCheck(
                global.toBeChecked.get(signedInfo.signatureID)
              );
              const hashRetStr = global.HashRet
                ? ""
                : "";
              document.getElementById("VerifyRet").innerText =
                hashRetStr + " " + signRetStr;
            }, 1000);
          }
        });
      } catch (e) {
        console.log(e);
      }
      if (!global.VerifyRet) {
        div.setAttribute("class", "gray");
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.upload-icon {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  height: 28px;
  padding-left: 10px;
  padding-right: 10px;
  background-color: rgb(59, 95, 232);
  border-radius: 1px;
  border-color: #5867dd;
  font-weight: 500;
  font-size: 12px;
  color: white;
  margin: 1px;
}

.scale-icon {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  width: 33px;
  height: 28px;
  background-color: #f5f5f5;
  border-radius: 1px;
  font-weight: 500;
  font-size: 12px;
  color: #333333;
  text-align: center;
  padding: 2px;
}
.scale-icon :active {
  color: rgb(59, 95, 232);
}
.scale-icon :hover {
  color: rgb(59, 95, 232);
}

.text-icon {
  display: flex;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  height: 28px;
  width: 90%;
  background-color: rgb(59, 95, 232);
  border-radius: 1px;
  border-color: #5867dd;
  font-weight: 500;
  font-size: 10px;
  color: white;
  margin-top: 20px;
}

.hidden {
  display: none !important;
}

.SealContainer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.SealContainer .mask {
  background: #000000;
  opacity: 0.3;
}

.content-title {
  font-size: 16px;
  text-align: center;
  border-bottom: 1px solid rgb(59, 95, 232);
  color: rgb(59, 95, 232);
  margin-top: 10px;
}

.SealContainer-content {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  background: white;
  display: flex;
  flex-direction: column;
  padding: 10px;
  align-items: center;
}

.SealContainer-layout {
  position: relative;
  width: 60%;
  height: 80vh;
  overflow-y: auto;
  background: white;
  z-index: 100;
  display: flex;
  flex-direction: column;
  padding: 10px;
  align-items: center;
}

.subcontent {
  width: 80%;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-bottom: 10px;
  font-family: simsun;
}

.subcontent .title {
  font-weight: 600;
}

.subcontent .value {
  font-weight: 400;
  -webkit-line-clamp: 1;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.left-section {
  position: fixed;
  width: 88px;
  height: 100%;
  background: #f5f5f5;
  border: 1px solid #e8e8e8;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.main-section {
  padding-top: 20px;
  margin-left: 88px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #808080;
  overflow: hidden;
}

@media (max-width: 767px) {
  .SealContainer-layout {
    position: relative;
    width: 90%;
    height: 90vh;
    overflow-y: auto;
    background: white;
    z-index: 100;
    display: flex;
    flex-direction: column;
    padding: 10px;
    align-items: center;
  }

  .subcontent {
    width: 95%;
    display: flex;
    flex-direction: column;
    text-align: left;
    margin-bottom: 10px;
    font-family: simsun;
  }

  .left-section {
    position: fixed;
    width: 0px;
    height: 100%;
    background: #f5f5f5;
    border: 1px solid #e8e8e8;
    align-items: center;
    display: none;
    flex-direction: column;
  }

  .main-section {
    padding-top: 20px;
    margin-left: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #808080;
    overflow: hidden;
  }
}
</style>
